.PostItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 5px;
  background-color: #fafafd;
  line-height: 1.5;

  &__button {
    margin-left: 10px;
  }

  &__user-button {
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: bold;
    background-color: darkcyan;

    &:hover {
      background-color: lightblue;
    }
  }
}
